"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Image from "next/image";
import { Shimmer } from "@/components/common/Shimmer";

const banners = [
  {
    src: "/image/webBanner1.jpg",
    link: "summer-sale",
    title: "Big Summer Discount",
  },
  {
    src: "/image/webBanner2.jpg",
    link: "new-arrivals",
    title: "New Collection 2025",
  },
  {
    src: "/image/webBanner3.jpg",
    link: "",
    title: "Flash Deals",
  },
  {
    src: "/image/webBanner4.jpg",
    link: "electronics",
    title: "Best Gadgets",
  },
  {
    src: "/image/webBanner5.jpg",
    link: "fashion",
    title: "Trendy Styles",
  },
];

export default function ImageCarousel() {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);
  const autoplayRef = useRef<NodeJS.Timeout | null>(null);

  const startAutoplay = () => {
    if (banners.length <= 1) return;
    autoplayRef.current = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % banners.length);
    }, 5000);
  };

  const stopAutoplay = () => {
    if (autoplayRef.current) {
      clearInterval(autoplayRef.current);
      autoplayRef.current = null;
    }
  };

  useEffect(() => {
    if (!isPaused) {
      startAutoplay();
    }
    return () => stopAutoplay();
  }, [isPaused]);

  const handleDotClick = (index: number) => {
    setCurrentIndex(index);
    setIsPaused(true);
    stopAutoplay();
    setTimeout(() => setIsPaused(false), 10000);
  };

  // Swipe support
  const touchStartX = useRef<number | null>(null);
  const touchEndX = useRef<number | null>(null);

  const handleTouchStart = (e: React.TouchEvent<HTMLDivElement>) => {
    touchStartX.current = e.touches[0].clientX;
  };

  const handleTouchMove = (e: React.TouchEvent<HTMLDivElement>) => {
    touchEndX.current = e.touches[0].clientX;
  };

  const handleTouchEnd = () => {
    if (touchStartX.current !== null && touchEndX.current !== null) {
      const distance = touchStartX.current - touchEndX.current;
      if (distance > 50) {
        setCurrentIndex((prev) => (prev + 1) % banners.length);
      } else if (distance < -50) {
        setCurrentIndex((prev) => (prev - 1 + banners.length) % banners.length);
      }
    }
    touchStartX.current = null;
    touchEndX.current = null;
  };

  if (banners.length === 0) return null;

  return (
    <section className="mt-7.5 w-full">
      <div
        className="group relative w-full overflow-hidden rounded-xl md:rounded-2xl bg-gray-200"
        style={{ aspectRatio: "1.97 / 1" }}
        onMouseEnter={() => setIsPaused(true)}
        onMouseLeave={() => setIsPaused(false)}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        {banners.map((banner, index) => {
          const isActive = index === currentIndex;
          const alt = banner.title || `Slide ${index + 1}`;

          return (
            <div
              key={index}
              className={`absolute inset-0 transition-opacity duration-700 ${
                isActive ? "opacity-100 z-10" : "opacity-0 z-0"
              }`}
            >
              {banner.link ? (
                <Link href={`/search/${banner.link}`} className="block h-full w-full">
                  <div className="relative h-full w-full bg-gray-100">
                    <Shimmer className="h-full w-full" />
                    <Image
                      src={banner.src}
                      alt={alt}
                      fill
                      className="object-contain w-full h-full"
                      priority={index === 0}
                      sizes="100vw"
                    />
                  </div>
                </Link>
              ) : (
                <div className="relative h-full w-full bg-gray-100">
                  <Shimmer className="h-full w-full" />
                  <Image
                    src={banner.src}
                    alt={alt}
                    fill
                    className="object-contain w-full h-full"
                    priority={index === 0}
                    sizes="100vw"
                  />
                </div>
              )}
            </div>
          );
        })}

        {/* Original dots style â€” exactly as you liked */}
        {banners.length > 1 && (
          <div className="absolute bottom-4 left-1/2 z-20 flex -translate-x-1/2 gap-2 rounded-full bg-black/30 px-3 py-2 backdrop-blur-sm md:bottom-6">
            {banners.map((_, index) => (
              <button
                key={index}
                onClick={() => handleDotClick(index)}
                className={`h-2.5 rounded-full transition-all duration-300 cursor-pointer ${
                  index === currentIndex
                    ? "w-8 bg-white"
                    : "w-2.5 bg-white/50 hover:bg-white/80 hover:w-4"
                }`}
                type="button"
                aria-label={`Go to slide ${index + 1}`}
                aria-current={index === currentIndex ? "true" : "false"}
              />
            ))}
          </div>
        )}
      </div>
    </section>
  );
}